@use "../common/mixins";
@use "../common/variables" as *;

.comment {
  @include mixins.container;

  @media #{$media-t} {
    display: grid;
    grid-template-columns: 1fr auto;
  }
}


.comment__quote {
  font-size: 15px;
  line-height: 30px;
  color: var(--light-black);
  margin: 0;

  @media #{$media-t} {
    font-size: 17px;
    display: contents;
  }
}

.comment__text {
  @media (min-width: 678px) {
    grid-row-start: 2;
    grid-column-start: 1;
    grid-column-end: 3;
  }
}

.comment__author {
  font-weight: 700;

  @media #{$media-t} {
    font-size: 18px;
  }
}

.comment__author-login {
  font-weight: 400;
  line-height: 30px;
}

.comment__nav {
  display: flex;

  @media #{$media-t} {
    grid-row-start: 3;
    grid-column-start: 2;
  }
}

.comment__button {
  @include mixins.link;

  @media #{$media-t} {
    grid-row-start: 1;
    grid-column-start: 2;
  }
}
